@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.question-container{display:flex;flex-direction:column;justify-content:space-around;height:300px;align-items:center;margin:2% 25%;width:50%;box-shadow:4px 4px 8px rgba(0,0,0,0.25);padding:5% 0;border-radius:10px;font-family:"Lato",sans-serif}.question-container select,.question-container textarea{font-family:"Lato",sans-serif}.interview-container form div{display:flex;flex-direction:column;align-items:center}.interview-container form div input{margin:1% 0;padding:0 1%}.saved-btn,.update-btn,.add-btn,.completed-btn,.create-interview-btn,.password-btn{background:none;border:none;padding:2% 5%;color:white;border-radius:5px;transition:all 300ms linear;cursor:pointer;font-family:"Lato",sans-serif}.saved-btn:hover,.update-btn:hover,.add-btn:hover,.completed-btn:hover,.create-interview-btn:hover,.password-btn:hover{transform:translateY(-10%);box-shadow:6px 6px 10px rgba(0,0,0,0.25)}.saved-btn:focus,.update-btn:focus,.add-btn:focus,.completed-btn:focus,.create-interview-btn:focus,.password-btn:focus{outline:none}.saved-btn{background:linear-gradient(to right, #11998e, #38ef7d)}.update-btn{background:linear-gradient(to right, #f12711, #f5af19)}.password-btn{box-shadow:4px 4px 4px rgba(0,0,0,0.25),-8px -8px 12px rgba(255,255,255,0.3)}.add-btn{background:linear-gradient(to left, #7f7fd5, #86a8e7, #91eae4);padding:1% 3%;margin:3% 0}.completed-btn{background:linear-gradient(to right, #f12711, #f5af19);padding:1% 3%;margin:3% 0}.create-interview-btn{background:linear-gradient(to left, #7f7fd5, #86a8e7, #91eae4);text-decoration:none;padding:1% 3%}.btns{display:flex;flex-direction:column;justify-content:center;align-items:center}.errorMsg{color:red}.dash-interview-container{display:flex;justify-content:flex-start;flex-wrap:wrap;margin:5% 15%}.dash-interview-container .interview-card{box-shadow:4px 4px 8px rgba(0,0,0,0.25);padding:2% 5%;border-radius:10px;margin:1% 3% 1% 0;background:linear-gradient(to right, #bdc3c7, #2c3e50);color:white;font-weight:bold;text-decoration:none;transition:all 300ms linear}.dash-interview-container .interview-card:hover{transform:translateY(-10%)}.interview-header{margin:5% 0}.interview-nav_home{display:flex;flex-direction:column;align-items:center}.interview-nav_home a:last-child{margin:2% 0 0 0}.detailed-question{font-family:"Lato",sans-serif;display:flex;flex-direction:column;justify-content:center;align-items:flex-start;margin:0 5%}.border-bottom{border-bottom:2px solid black;height:2px;width:100%}.interview-score{color:green;font-weight:bold}.interview-type{text-transform:capitalize;color:orange;font-weight:bold}.interview-nav{display:flex}.interview-nav a{margin:2% 3% 2% 5%;transition:all 300ms linear}.interview-nav a:hover{transform:translateY(-10%)}.interaction-buttons{display:flex;justify-content:center;width:100%}.interaction-buttons a{margin:0 5%}.delete-interaction{display:flex;flex-direction:column;height:300px;justify-content:space-around;align-items:center}.delete-interaction input{padding:1% 3%}.password-div{display:flex;flex-direction:column;justify-content:space-around;align-items:center;height:300px}.password-div input{height:35px;width:300px;border-radius:5px;border:none}.password-div input:focus{outline:none}.password-container{background:#c3c9ed;height:100vh;display:flex;justify-content:center}.password-container h1{color:white}.password-intro{flex:0.65 1;margin:8% 0;border-right:2px solid #8894db}.password-intro div{text-align:center;font-family:"Lato",sans-serif}.password-intro div p{margin:3% 0 0 0;color:white;font-weight:bold}.password-entry{margin:8% 3%}.nav-btn{display:flex;justify-content:center;align-items:center;margin:2% 0}.nav-btn p{color:#8894db;font-weight:bold;font-family:"Lato",sans-serif}h1{font-family:"Lato",sans-serif;margin:0;padding:0}

